import { Icon, Carousel, Button } from 'antd-mobile';
import React, { useState } from 'react';
import { useHistory } from 'react-router-dom';
import answerSheet from '../Topic/answerSheet';
import resultSheet from '../Topic/resultSheet';
import {
  Page,
  HeaderBox,
  GrayFont,
  FontContent,
  CarouselItem,
  CarouseBox,
  ButtonBox,
  ButtonBoxInner,
  CardBox,
  CardItem,
  EditorFont,
  EditorIcon,
} from '../styles';

const Exam: React.FC = () => {
  const { openModal } = answerSheet();
  const { openModal: openResult } = resultSheet();
  const [answerMap, setAnswerMap] = useState({});
  const history = useHistory();

  const handleEditor = (type: string, value: string): void => {
    const newAnswer = {
      ...answerMap,
      [type]: value,
    };
    setAnswerMap(newAnswer);
  };
  const labelArr = ['AB', 'CDE', 'FG', 'HIJ', 'MN'];
  return (
    <Page>
      <HeaderBox>
        <Icon
          type="cross"
          color="#262626"
          onClick={(): void => history.go(-2)}
        />
        <GrayFont>00:18</GrayFont>
      </HeaderBox>
      <CarouseBox>
        <Carousel dots={false} style={{ height: '100%' }}>
          <CarouselItem>
            <HeaderBox className="lr-pd">
              <GrayFont>中心考试 2020年6月数学卷</GrayFont>
              <GrayFont>1/5</GrayFont>
            </HeaderBox>
            <FontContent>
              第一题
              <br />
              XXXXXXXXXXXXXABXXXXXXXXXXXXXXXXXHIJXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXX（）。
            </FontContent>
            <CardBox>
              <CardItem>解答</CardItem>
              {labelArr.map((item) => (
                <CardItem key={item}>
                  <span>{item}</span>
                  <EditorFont>
                    <span>{answerMap[item as keyof typeof answerMap]}</span>
                    <EditorIcon
                      onClick={(): void =>
                        openModal({
                          label: item,
                          changeAnswer: (value) => handleEditor(item, value),
                        })
                      }
                    />
                  </EditorFont>
                </CardItem>
              ))}
            </CardBox>
          </CarouselItem>
          <CarouselItem>
            <HeaderBox className="lr-pd">
              <GrayFont>中心考试 2020年6月数学卷</GrayFont>
              <GrayFont>2/5</GrayFont>
            </HeaderBox>
            <FontContent>
              第二题
              <br />
              XXXXXXXXXXXXXABXXXXXXXXXXXXXXXXXHIJXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXXX（）。
            </FontContent>
            <CardBox>
              <CardItem>解答</CardItem>
              {labelArr.map((item) => (
                <CardItem key={item}>
                  <span>{item}</span>
                  <EditorFont>
                    <span>{answerMap[item as keyof typeof answerMap]}</span>
                    <EditorIcon
                      onClick={(): void =>
                        openModal({
                          label: item,
                          changeAnswer: (value) => handleEditor(item, value),
                        })
                      }
                    />
                  </EditorFont>
                </CardItem>
              ))}
            </CardBox>
          </CarouselItem>
        </Carousel>
      </CarouseBox>
      <ButtonBox>
        <ButtonBoxInner>
          <Button type="primary" onClick={() => openResult({ type: 'exam' })}>
            提交并查看结果
          </Button>
        </ButtonBoxInner>
      </ButtonBox>
    </Page>
  );
};

export default Exam;
